.additional-product__title {
  font-family: $main-font;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: #000000;
}

.additional-product__name {
  font-family: $main-font;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px;
  color: #222222;
}

.additional-product__pack {
  font-family: $title-font;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}

.additional-product__price {
  font-family: $title-font;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
}

.additional-product__bonus {
  background: url("../img/catalog/cat_in_backpack.png");
  background-repeat: no-repeat;
  background-color: rgba(104, 183, 56, 0.85);
}

.additional-product__bonus-title {
  font-family: $title-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}

.additional-product__list {
  display: flex;
  flex-direction: column;
}

@media (min-width: $tablet-width) {
  .additional-product__item {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: $desktop-width) {
  .additional-product__item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
